body {
  background-color: #75cfe5;
}

.stage {
  height: 400px;
  width: 400px;
  margin: 0 auto;
}

.ball {
  width: 100%;
  height: 100%;
  -webkit-transform-style: preserve-3d;
  background-image: url("http://www.jquerypost.com/demo/animated-world-map-with-round-circle-in-css-847/earth.jpg");
  background-repeat: repeat-x;
  background-size: auto 200px;
  border-radius: 50%;
  margin: 0;
  animation: move-map 35s cubic-bezier(.5, .5, .5, .5) infinite;
  display: inline-block;
  position: relative;
  box-shadow: 0 2px 2px rgba(0, 0, 0, .18), 0 4px 4px rgba(0, 0, 0, .18), 0 8px 8px rgba(0, 0, 0, .18), 0 16px 16px rgba(0, 0, 0, .15), 0 32px 32px rgba(0, 0, 0, .15);
}

.ball:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 2;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: inset -40px 10px 70px 10px rgba(0, 0, 0, .5);
}

.ball:after {
  content: "";
  width: 100%;
  height: 100%;
  opacity: .3;
  background: radial-gradient( circle at 50% 80%, ligthen(#75cfe5, 5), #75cfe5 10%, #020409 66%, #13265e 100% );
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes move-map {
  0% {
    background-position: -800px 40%;
  }

  100% {
    background-position: 0 40%;
  }
}

